@import "../../../../variable.scss";

.review {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;

  &-content {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    width: 100%;

    &-item {
      display: flex;

      .left {
        padding: 0.7rem 0;
        width: 15rem;
        font-weight: 600;
        flex-shrink: 0;
      }

      .right {
        padding: 0.7rem 0;

        &.gridImg {
          display: grid;
          grid-template-columns: repeat(5, 1fr);
          gap: 1rem;
          padding: 0;

          .right-imgWrapper {
            width: 100%;
            height: 10rem;
            
            > img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }

    &-btnWrapper {
      display: flex;
      justify-content: center;
      column-gap: 1rem;
      
      button {
        padding: 1rem 2rem;
        border: none;
        color: #fff;
        background-color: $sub-color;
        transition: background-color .2s ease;
        cursor: pointer;

        &:hover {
          background-color: $main-color
        }
      }
    }
  }
}